import * as actionsType from '../actions/actionType';

const initilizeState = {
    ingredient:null,
    totalPrice: 4,
    error: false,
    building: false
};
//global variable should use capital letter
const INGREDIENT_PRICES = {
    salad: 0.5,
    cheese:0.4,
    meat:1.3,
    bacon:0.7
};

const reducer = (state = initilizeState, action) => {
    switch(action.type){
        case actionsType.ADD_INGREDIENT:
        return{
            ...state,
            ingredient:{
                ...state.ingredient,
                [action.ingredientName] : state.ingredient[action.ingredientName] + 1
            },
            totalPrice: state.totalPrice + INGREDIENT_PRICES[action.ingredientName],
            building: true
        };
        case actionsType.REMOVE_INGREDIENT:
            return{
                ...state,
                ingredient:{
                    ...state.ingredient,
                    [action.ingredientName] : state.ingredient[action.ingredientName] - 1
                },
                totalPrice: state.totalPrice - INGREDIENT_PRICES[action.ingredientName],
                building: true
            };
        case actionsType.SET_INGREDIENT:
            return {
                ...state,
                ingredient:{
                    salad: action.ingredient.salad,
                    bacon: action.ingredient.bacon,
                    cheese: action.ingredient.cheese,
                    meat: action.ingredient.meat
                },
                totalPrice: 4,
                error: false,
                building: false
            };
        case actionsType.FETCH_INGREDIENT_FAIL :
            return{
                ...state,
                error: true
            };
        default:
            return state;
    }
   
}

export default reducer;