import React, { Component } from 'react';
import Modal from '../../components/UI/Modal/Modal';
import Auxilliary from '../Axilliary';

const withErrHandler = (WrapComponent, axios) =>{
    return class extends Component{
        state = {
            error:null
        }
        UNSAFE_componentWillMount(){
            this.interRequest = axios.interceptors.request.use(req =>{
                this.setState({error: null});
                return req;
            });
            this.interResponse = axios.interceptors.response.use(res=> res, error=>{
                this.setState({error: error});
            });
        }
        componentWillUnmount(){
            axios.interceptors.request.eject(this.interRequest);
            axios.interceptors.response.eject(this.interRequest);
        }
        errorComfirmed = () =>{
            this.setState({error: null});
        }
        render(){
            return(
            <Auxilliary >
              <Modal show ={this.state.error} modalClose={this.errorComfirmed}>
                  {this.state.error ?this.state.error.message: null}
              </Modal>
              <WrapComponent {...this.props} />
            </Auxilliary>
            )
        }
    } 
}

export default withErrHandler;