import React, { Component } from 'react';
import { connect } from 'react-redux';
import Auxilliary from '../../hoc/Axilliary';
import Burger from '../../components/Burger/Burger';
import BuildControl from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import Spinner from '../../components/UI/Spinner/spinner';
import withErrHandler from '../../hoc/withErrHandler/withErrHandler';
import * as actions from '../../store/actions/index';
import axios from '../../axios';

 export class Burgerbuilder extends Component{
   /*  constructor(props){
        super(props)
        this.state ={}
    } */
    state ={
        purchasing:false,
    };
    componentDidMount(){
        this.props.initIngredient();
    }
    updatePurchaseState = ingredients => {
        //coverting object to array
        const sum  = Object.keys(ingredients)
            .map(isKey=>{
                return ingredients[isKey]
            }).reduce((sum, el)=>{
                return sum + el;
            }, 0);
           return sum > 0;
    }
    
    puchaseHandler = () =>{
        if(this.props.isAuthenticated){
            this.setState({purchasing: true});
        }else{
            this.props.onSetRedirectPath('/checkout');
            this.props.history.push('/auth');
        }
        
    }
    purchaseCancelHandler = () =>{
        this.setState({purchasing: false})
    }
    purchaseContinueHandler = () =>{
        this.props.onpurchaseRedirect();
        this.props.history.push('/checkout');
    }
    render(){
        let disabledInfo ={
            ...this.props.ings
        };
        let burger =this.props.error ? <p style={{textAlign:'center'}}>
            Ooops could not load ingredients</p>: <Spinner />;
        let orderSummary = null;
        if(this.props.ings){
            burger =(
            <Auxilliary>
            <Burger  ingredient ={this.props.ings}/>
            <BuildControl 
            ingredientAdded ={this.props.onAddingredient}
            ingredientRemoved ={this.props.onRemoveingredient}
            disabled={disabledInfo}
            purchasable ={this.updatePurchaseState(this.props.ings)}
            price={this.props.price}
            isAuth={this.props.isAuthenticated}
            ordered ={this.puchaseHandler}
            />
            </Auxilliary>
            
            )
            orderSummary = <OrderSummary ingredient ={this.props.ings}
            price={this.props.price}
            purchaseCancel ={this.purchaseCancelHandler}
            purchaseContinue ={this.purchaseContinueHandler}/>
            // eslint-disable-next-line no-unused-vars
            for (let key in disabledInfo){
                disabledInfo[key] = disabledInfo[key] <=0;
            }
        }
        
        /* if(this.state.loading){
            orderSummary = <Spinner />
        } */
        return(
            <Auxilliary>
            <Modal show ={this.state.purchasing} modalClose={this.purchaseCancelHandler}>
              {orderSummary}  
            </Modal>
            {burger}
            </Auxilliary>
        )
    }
}
const mapStateToProps = state => {
    return{
        ings: state.burgerBuilder.ingredient,
        price: state.burgerBuilder.totalPrice,
        error: state.burgerBuilder.error,
        isAuthenticated: state.auth.token !== null
    }
};
const mapDispatchToProps = dispatch => {
    return{
        onAddingredient: (ingName)=> dispatch(actions.addIngredient(ingName)),
        onRemoveingredient: (ingName)=> dispatch(actions.removeIngredient(ingName)),
        initIngredient: ()=> dispatch(actions.initIngredient()),
        onpurchaseRedirect : ()=> dispatch(actions.purchaseinit()),
        onSetRedirectPath : (path)=> dispatch(actions.redirectAuthpath(path))
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(withErrHandler(Burgerbuilder, axios));
