import React, {Component} from 'react'
import Auxilliary from '../../../hoc/Axilliary';
import Button from '../../UI/Button/Button';
class OrderSummary extends Component{
    /* componentDidUpdate(){
        console.log('component will update');
    } */
    render(){
     const ingredientsSummary =Object.keys(this.props.ingredient)
    .map(isKey =>{
        return (
        <li key={isKey}>
            <span style={{textTransform:'capitalize'}}>{isKey}</span> : {this.props.ingredient[isKey]}
        </li>
        )
    });
        return(
        <Auxilliary>
            <h3>Your Order</h3>
            <p>A delicios Burger with the following ingredients : </p>
            <ul>
                {ingredientsSummary }
            </ul>
            <p><strong>Total Price : {this.props.price.toFixed(2)}</strong></p>
            <p>Continue to Checkout ?</p>
            <Button btnType="Danger" clicked={this.props.purchaseCancel}>CANCEL</Button>
            <Button btnType ="Success" clicked={this.props.purchaseContinue}>CONTINUE</Button>
        </Auxilliary>
        )
    }
}
export default OrderSummary;