import * as actionType from './actionType';
import axios from '../../axios';

export const purchaseBurgerSuccess = (id, orderData) => {
    return{
        type: actionType.PURCHASE_BURGER_SUCCESS,
        id: id,
        orderData: orderData
    };
};

export const purchaseBurgerFail = error => {
    return{
        type: actionType.PURCHASE_BURGER_FAIL,
        error: error
    };
};

export const purchaseBurgerStart = () => {
    return{
        type:actionType.PURCHASE_BURGER_START
    };
};

export const purchaseBurger = ( orderData , token) => {
    return dispatch => {
        dispatch(purchaseBurgerStart());
        axios.post('/orders.json?auth=' + token, orderData)
        .then(response =>{
            dispatch(purchaseBurgerSuccess(response.data.name, orderData))
        }).catch(error=>{
            dispatch(purchaseBurgerFail(error))
            //
        })
    };
};

export const purchaseinit = () =>{
    return{
        type: actionType.PURCHASE_INIT
    }
}

export const fetchPurchasesuccess = order => {
    return{
        type: actionType.FETCH_ORDER_SUCCESS,
        orders: order
    };
};

export const fetchPurchasefail = error => {
    return{
        type: actionType.FETCH_ORDER_FAIL,
        error: error
    };
};

export const fetchPurchasestart = () => {
    return{
        type: actionType.FETCH_ORDER_START
    };
};

export const fetchOrder = (token, userId) => {
    return dispatch =>{
        dispatch(fetchPurchasestart());
        const queryParams = '?auth='+ token + '&orderBy="userId"&equalTo="'+userId + '"' ;
        axios.get('/orders.json' + queryParams)
    .then(res =>{
        const fetchData = [];
        // eslint-disable-next-line no-unused-vars
        for(let key in res.data){
            fetchData.push({
                ...res.data[key],
                id: key
            })
        }
        dispatch(fetchPurchasesuccess(fetchData));
    }).catch(error =>{
        dispatch(fetchPurchasefail(error));
    });
    }
};
