import React from 'react';
import Classes from './burger.module.css';
import BurgerIngredient from './Burgeringredient/Burgeringredient';
const burger = props =>{
     //covert the object props recieved to array
    let modifiedIngredient = Object.keys(props.ingredient)
    .map(igKey =>{
        return [...Array(props.ingredient[igKey])].map((_, i) =>{  
           return  <BurgerIngredient key={igKey + i} type={igKey}/>
        });
       //reduce is a built in javascript use to reduce or remove array, 
    }).reduce((preValue, curentValue) =>{
         return preValue.concat(curentValue);
    }, []);
    if(modifiedIngredient.length === 0){
        modifiedIngredient = <p>Please Start Adding ingredients !</p>;
    }
    return (
    <div className={Classes.Burger}>
        <BurgerIngredient type="bread-top"/>
        {modifiedIngredient }
        <BurgerIngredient type ="bread-bottom"/>
    </div>
)
}

export default burger;