import * as actionType from './actionType';
import axios from 'axios';
const apiKey = 'AIzaSyAYY-hRPbqhS1KGGEoC-6Tot0GjsvfVCmg';
export const authStart = () => {
    return{
        type:actionType.AUTH_START
    };
};

export const authSuccess = (token, userid) => {
    return{
        type:actionType.AUTH_SUCCESS,
        IdToken: token,
        userId: userid
    };
};

export const authFail = (error) => {
    return{
        type:actionType.AUTH_FAIL,
        error: error
    };
};

export const logout = () =>{
    localStorage.removeItem('token');
    localStorage.removeItem('expirationDate');
    localStorage.removeItem('userId');
    return{
        type: actionType.AUTH_LOGOUT
    };
};
export const checkAuthTimeout = (expireTime) => {
    return dispatch => {
        setTimeout(()=> {
            dispatch(logout());
        }, Number(expireTime)* 1000);
    };
};

export const auth = (email, password, isSignup) => {
    return dispatch =>{
        dispatch(authStart());
        const authData = {
            email: email,
            password: password,
            returnSecureToken: true
        };
        let apiURL = null;
         if(isSignup){
            apiURL ='https://identitytoolkit.googleapis.com/v1/accounts:signUp?key='+ apiKey;
         }
         else
        {
             apiURL ='https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key='+ apiKey;
        } 
        axios.post(apiURL, authData)
        .then(response =>{
            const expirationDate = new Date(new Date().getTime() + response.data.expiresIn *1000);
            localStorage.setItem('token', response.data.idToken);
            localStorage.setItem('expirationDate', expirationDate);
            localStorage.setItem('userId',  response.data.localId);
            dispatch(authSuccess(response.data.idToken, response.data.localId));
            //dispatch(checkAuthTimeout(response.data.expiresIn));
        }).catch(error =>{
            dispatch(authFail(error.response))
        })
    }
};

export const redirectAuthpath = path => {
    return{
        type:actionType.SET_AUTH_REDIRECT_PATH,
        path: path
    };
};

export const authState = () => {
    return dispatch => {
        const token = localStorage.getItem('token');
        const expirationDate = new Date(localStorage.getItem('expirationDate'));
        const userId = localStorage.getItem('userId');
        if(!token){
            dispatch(logout());
        }else{
            if(expirationDate <= new Date()){
                dispatch(logout());
            }else{
                dispatch(authSuccess(token, userId));
                dispatch(checkAuthTimeout((expirationDate.getTime() - new Date().getTime())/ 1000));
            }
        }
    };
};

